<script>
	export default {
		data() {
			return {
				ishow: false,
				is_lia_show: false,
				screenWidth: document.documentElement.clientHeight,
				content: '',
				limttle: ['一定位', '二定位', '三定位', '四定位', '二字现', '三字现'],
				indexs: 0 //俩面盘点击效果
			}
		},
		props: {
			endTime: {
				type: String,
				default: '1515400000'
			},
			endText: {
				type: String,
				default: '00 : 00 : 00'
			}
		},
		methods: {
			kuaij() {
				$('.zongjis #j-all').eq(this.indexs).find('.j-odds').hide();
			},
			yiban() {
				$('.zongjis #j-all').eq(this.indexs).find('.j-odds').show();
			},
			lianm(index) {
				this.indexs = index
				console.log(this.indexs)
			},
			countdowm(timestamp) {
				let self = this;
				let timer = setInterval(function() {
					let nowTime = new Date();
					let endTime = new Date(timestamp * 1000);
					let t = endTime.getTime() - nowTime.getTime();
					if(t > 0) {
						let day = Math.floor(t / 86400000);
						let hour = Math.floor((t / 3600000) % 24);
						let min = Math.floor((t / 60000) % 60);
						let sec = Math.floor((t / 1000) % 60);
						hour = hour < 10 ? "0" + hour : hour;
						min = min < 10 ? "0" + min : min;
						sec = sec < 10 ? "0" + sec : sec;
						let format = '';
						if(day > 0) {
							format = `${day} : ${hour} : ${min} : ${sec}`;
						}
						if(day <= 0 && hour > 0) {
							format = `${hour} : ${min} : ${sec}`;
						}
						if(day <= 0 && hour <= 0) {
							format = `${min} : ${sec}`;
						}
						self.content = format;
					} else {
						clearInterval(timer);
						self.content = self.endText;
					}
				}, 1000);
			}
		},
		mounted: function() {
			let that = this;
			window.onresize = function() {
				that.screenWidth = document.documentElement.clientHeight;
			}
			this.countdowm(this.endTime);

		}

	}
</script>
